$(function () {
    $('.gallery__slider').slick({
        loop: true,
        infinite: true,
        slidesToShow: 4,
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed: 2500,
        dots: false,
        arrows: false,
        responsive: [
            {
                breakpoint: 1322,
                settings: {
                    slidesToShow: 3,
                },
            },
            {
                breakpoint: 950,
                settings: {
                    slidesToShow: 2,
                },
            },
            {
                breakpoint: 650,
                settings: {
                    slidesToShow: 1,
                },
            },
        ],
    });
});

